/* ANIMATION SPINNER */
.spinny-set {
  width: 600px;
  height: 600px;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  align-content: center;
}

.spinny-set > * {
  grid-column: 1;
  grid-row: 1;
}

.spinny-set_img {
  height: 200px;
  width: 200px;
}

.spin-item_1 {
  animation: spinAround_1 10s linear infinite;
}

.spin-item_1_img {
  height: 200px;
  width: 200px;
}

.spin-item_2_img {
  height: 100px;
  width: 100px;
}

@keyframes spinAround_1 {
  from {
    transform: rotate(0deg) translate(300px) scale(0.7);
  }

  to {
    transform: rotate(360deg) translate(300px) scale(0.7);
  }
}

.spin-item_2 {
  animation: spinAround_2 10s linear infinite;
}

@keyframes spinAround_2 {
  from {
    transform: rotate(0deg) translate(260px);
  }

  to {
    transform: rotate(360deg) translate(260px);
  }
}

@media only screen and (max-width: 768px) {
  .spinny-set {
    width: 400px;
    height: 400px;
  }

  .spinny-set_img {
    height: 120px;
    width: 120px;
  }

  .spin-item_1_img {
    height: 120px;
    width: 120px;
  }

  @keyframes spinAround_1 {
    from {
      transform: rotate(0deg) translate(260px) scale(0.7);
    }

    to {
      transform: rotate(360deg) translate(260px) scale(0.7);
    }
  }

  .spin-item_2_img {
    height: 70px;
    width: 70px;
  }

  @keyframes spinAround_2 {
    from {
      transform: rotate(0deg) translate(180px);
    }

    to {
      transform: rotate(360deg) translate(180px);
    }
  }
}

