/* ////////////////////////// */

@media only screen and (min-width: 768px) {
  .desktop-show {
    display: block;
    text-align: justify;
  }

  .mobile-show {
    display: none;
  }
}

@media only screen and (max-width: 1199px) {
  .Protect-area h3 {
    font-size: 2rem;
  }

  .security-title {
    font-size: 3.2rem;
  }
}

@media only screen and (max-width: 1024px) {
  .security-title {
    font-size: 3rem;
  }

  .Protect-area-text {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 991px) {
  .security-title {
    font-size: 2rem;
  }

  .Protect-area-text {
    font-size: 1.2rem;
  }

  .safer-title h3 {
    font-size: 2rem;
  }

  .safer-title p {
    max-width: 100%;
  }

  .Protect-area h3 {
    font-size: 1.5rem;
  }

  .Authentication .ui.card > .content {
    min-height: 160px;
  }

  .protect-data {
    background-image: none !important;
  }
}

@media only screen and (max-width: 767px) {
  .ui.grid > .row > .column.width-full,
  .ui.grid > .row > .column.width-full-2 {
    width: 100% !important;
  }

  .securityapp {
    justify-content: center;
  }

  .security-outer {
    text-align: center;
  }

  .ui.grid > .row > .column.width-full-2 img {
    margin: 0 auto;
  }

  .ui.grid > .row > .column.emptyarea {
    display: none;
  }

  .ui.grid > .row > .column.security-left,
  .ui.grid > .row > .column.security-right {
    width: 50% !important;
  }

  .safer-title p,
  .security-outer p {
    text-align: justify;
  }

  .protect-data,
  .safer,
  .chating-area {
    background-image: none !important;
  }

  .desktop-show {
    display: none;
  }

  .mobile-show {
    display: block;
  }
}

@media only screen and (max-width: 620px) {
  .securityapp a img.ui.image {
    max-width: 120px;
  }

  .ui.grid > .row > .column.reachRight,
  .ui.grid > .row > .column.reachLeft {
    width: 100% !important;
  }

  .reachLeft img {
    margin: 0 auto;
  }

  .Protect-area {
    margin: 0;
  }

  .ui.grid > .row > .column.Protect-area-out {
    width: 100% !important;
  }

  .Protect-area-3 {
    justify-content: center;
    margin: 30px 0 0 0;
  }

  .three-section .ui.grid > .row > .column {
    width: 50% !important;
  }

  .Protect-area-text {
    text-align: justify;
  }

  .Authentication .ui.card > .content {
    padding: 0.5rem;
  }
}

@media only screen and (max-width: 480px) {
  .ui.grid > .row > .column.security-left,
  .ui.grid > .row > .column.security-right {
    width: 100% !important;
    margin: 15px 0;
  }

  .Protect-area h3 {
    font-size: 1.3rem;
  }
}

@media only screen and (max-width: 380px) {
  .three-section .ui.grid > .row > .column {
    width: 100% !important;
  }
}
